<template>
	<div style="margin:5px 0 0 10px;">
		<a-upload :action="this.$store.state.requestPath + 'uploadCarousel'" list-type="picture-card" :file-list="fileList"
		 @preview="handlePreview" @change="handleChange" :remove="handleRemove">
			<div v-if="fileList.length < 4">
				<div class="ant-upload-text">
					<a-icon type="plus"></a-icon>
					<div>上传</div>
				</div>
			</div>
		</a-upload>
		<a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
			<img alt="example" style="width: 100%" :src="previewImage" />
		</a-modal>
	</div>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex';

	function getBase64(file) {
		return new Promise((resolve, reject) => {
			const reader = new FileReader();
			reader.readAsDataURL(file);
			reader.onload = () => resolve(reader.result);
			reader.onerror = error => reject(error);
		});
	}
	export default {
		async created() {
			this.getCarousel();
		},
		computed: {
			...mapState(['userInfo', 'requestPath', 'picRequestPath'])
		},
		data() {
			return {
				previewVisible: false,
				previewImage: '',
				fileList: [],
			};
		},
		watch: {
			fileList: async function(data) {
				this.getCarousel();
			}
		},
		methods: {
			async getCarousel(){
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/getCarousel');
				let filename = data.data;
				this.fileList = [];
				for(var i=0; i<filename.length; i++){
					let file = {uid:-(i+1), name:filename[i], status:'done','url': this.picRequestPath+'carousel/'+filename[i]};
					this.fileList.push(file);
				}
			},
			handleCancel() {
				this.previewVisible = false;
			},
			async handlePreview(file) {
				if (!file.url && !file.preview) {
					file.preview = await getBase64(file.originFileObj);
				}
				this.previewImage = file.url || file.preview;
				this.previewVisible = true;
			},
			handleChange({
				fileList
			}) {
				this.fileList = fileList;
			},
			async handleRemove(file){
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/delCarousel/' + file.name);
				if(data.code == 0){
					this.$message.success('删除成功');
				}else{
					this.$message.error('删除失败');
				}
			}
		},
	};
</script>
<style lang="scss">

	.ant-upload-list-picture-card-container {
		float: left;
 		width: 600px;
		height: 280px;
		margin: 0 8px 8px 0 
	}

	.ant-upload-list-picture-card .ant-upload-list-item {
		float: left;
		width: 600px;
		height: 280px;
		margin: 0 15px 15px 0;
	}

	.ant-upload-select-picture-card i {
		width: 580px;
		height: 235px;
		font-size: 32px;
		margin-bottom: -80px;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		font-size: 32px;
		margin-top: 50px;
		color: #666;
	}
</style>
